import React from "react"
import Img from "gatsby-image/withIEPolyfill"
import { useStaticQuery, graphql } from "gatsby"

import styles from "./NotFound.module.scss"
import ButtonLink from "../components/ButtonLink"
import treeWithRainImage from "../images/treeWithRain.png"

export default () => {
  const { arrowContact } = useStaticQuery(graphql`
    query {
      arrowContact: file(relativePath: { eq: "arrowContact.png" }) {
        childImageSharp {
          fixed(width: 168, quality: 100) {
            ...GatsbyImageSharpFixed
          }
        }
      }
    }
  `)

  return (
    <div className={styles.container}>
      <div className={styles.pageContent}>
        <img src={treeWithRainImage} alt="" className={styles.pageImage} />
        <div className={styles.mainContent}>
          <div className={styles.heading}>404 NOT FOUND</div>
          <div className={styles.description}>
            <div>This page is being constructed.</div>
            <div>
              <div>Please contact the builder if you are interested in</div>
              <div>sharing the development progress.</div>
              <div className={styles.ctaButtonContainer}>
                <Img
                  fixed={arrowContact.childImageSharp.fixed}
                  objectFit="cover"
                  objectPosition="50% 50%"
                  alt=""
                />
                <ButtonLink
                  to="/contact"
                  title="Contact"
                  wrapperClassName={styles.ctaButton}
                />
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}
